

.navbar {
  background-color:transparent;
  height: 10vh;
  width: 100vw;
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  justify-content: space-around;
  position: fixed;
 
}

.navbar a, span {
  
  color: white;
  background-color: transparent;
  font-size: 2rem;
  text-decoration: none;
  text-shadow: #ff00c8;
}

.glow-on-hover {
  width: 18%;
  height: 100%;
  border: white;
  outline: none;
  color: #fff;
  background: #111;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
  box-shadow: 2px 5px #111;
}

.glow-on-hover:before {
  content: '';
  background: linear-gradient(45deg, #15aef0, #ff00c8);
  position: absolute;
  top: -2px;
  left:-2px;
  background-size: 400%;
  z-index: -1;
  filter: blur(15px);
  width: calc(100% + 8px);
  height: calc(100% + 8px);
  animation: glowing 10s linear infinite;
  opacity: 0;
  transition: opacity .1s ease-in-out;
  border-radius: 10px;
}

.glow-on-hover:active {
  color:  #1e8bf8
}

.glow-on-hover:active:after {
  background: transparent;
}

.glow-on-hover:hover:before {
  opacity: 1;
}

.glow-on-hover:after {
  z-index: -1;
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: #1e8bf8;
  left: 0;
  top: 0;
  border-radius: 10px;
}


@media screen and (max-width: 960px) {
  .navbar{
    height: 20vh;
    width: 100vw;
  }
  .navbar a, span {
    font-size: 1.3rem;
  }
  .glow-on-hover {
    height: 50%;
    width: 50%;

  }

}
@keyframes glowing {
  0% { background-position: 0 0; }
  50% { background-position: 400% 0; }
  100% { background-position: 0 0; }
}
