  .nightroad{
    height: 160px;
    width: 600%;
    background: url('../images/Img_02.png');
    display: block;
    position: fixed;
    bottom: 0%;
    left: 0%;
    background-repeat: repeat-x;
    animation: moveright 10s linear infinite;
  }

  @keyframes moveright{
    100%{transform: translateX(-2950px)}
  }
  .car:active{
    animation: zoom 5s linear
  }
  @keyframes zoom {
    100%{transform:translateX(2500px) translateY(-1px)}
    50%{transform:translateX(1000px) translateY(2px)}
    0%{transform:translateX(0px) translateY(-1px)}

  }

  .car {
    
    position: fixed;
    bottom: 2.5%;
    left: 5%;
    animation: suspension 1s linear infinite
  }

  @keyframes suspension{
    100%{transform:translateY(-1px)}
    50%{transform:translateY(2px)}
    0%{transform:translateY(-1px)}

  }

