.footer {
    height: 500px;
    background-color:rgb(64, 113, 211);
}

.footer-text {
    color: white;
    font-weight: bolder;

}



#footerIcons {
    color: white;
    font-size: 70px;
    animation-name: spin;
  animation-duration: 10ms;
  animation-iteration-count: infinite;
  animation-timing-function: linear; 
}

#footerIcons:hover {
    color:rgb(242, 132, 223);
    font-size: 40px;
    scale: 1.2;
    animation: none;
    transition: 0.2s ease all;
    border: none;
  
    transform: rotate(-4deg);
  }

@keyframes spin {
    from {
        transform:rotate(0deg);
    }
    to {
        transform:rotate(360deg);
    }

}

